 .footer, .body-footer {
   font-family: 'Vast Shadow', cursive;
 }

 .map-container {
   border-radius: 5px;
   border: 1px solid grey;
   width: 50%;
   height: 90%;
   align-content: center;
   margin: 10px auto
 }

 .map {
   width: 100%;
   height: 600px;
   align-content: center;
   border-radius: 15px;

 }

 .mapboxgl-map {
   height: 500px;
   width: 800px;
   margin: 0 auto;
 }

 .main-body {
   display: flex;
   height: 70%;
   margin: 20px;
 }

 .side-section {
  margin-left: 10px;
  width: 50%;
  height: 90%;
  padding-top: 5px;
 }
 
 .sidebar{
  height: 90%;
  width: 100%;
  align-items: center;
  border-radius: 15px;
  background: radial-gradient(grey, transparent);
}

 .photo {
   max-width: 650px;
   max-height: 500px;
 }

 .photo {
   align-content: center;
 }

.body-footer {
  bottom: 0px;
  color: white;
  font-weight: 500;
}